"use client";
import React, { useState } from "react";
import Link from "next/link";
import Image from "next/image";

const WOLF_LOGO = "/wolf-head-purple.png"; // public/wolf-head-purple.png
const CART_ICON = (
  <svg width="28" height="28" fill="none" viewBox="0 0 24 24" stroke="currentColor">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2 9m13-9l2 9m-5-9V6a2 2 0 10-4 0v3" />
  </svg>
);
const HAMBURGER_ICON = (
  <svg width="32" height="32" fill="none" viewBox="0 0 24 24" stroke="currentColor">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 8h16M4 16h16" />
  </svg>
);
const CLOSE_ICON = (
  <svg width="32" height="32" fill="none" viewBox="0 0 24 24" stroke="currentColor">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
  </svg>
);

const NAV_LINKS = [
  { label: "Features", href: "#features" },
  { label: "Products", href: "#products" },
  { label: "Reviews", href: "#reviews" },
];

const MobileNavbar: React.FC = () => {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <>
      {/* Navbar */}
      <nav className="w-full flex items-center justify-between px-4 py-3 bg-black text-white fixed top-0 left-0 z-30 md:hidden">
        {/* Logo */}
        <div className="flex items-center gap-2">
          <Image src={WOLF_LOGO} alt="Wolf Logo" width={36} height={36} />
        </div>
        {/* Site Name */}
        <div className="font-bold text-lg tracking-wide">LUTFACTORY</div>
        {/* Cart and Hamburger */}
        <div className="flex items-center gap-4">
          <Link href="/cart" aria-label="Cart" className="hover:text-purple-400">{CART_ICON}</Link>
          <button onClick={() => setMenuOpen(true)} aria-label="Open Menu" className="focus:outline-none">
            {HAMBURGER_ICON}
          </button>
        </div>
      </nav>

      {/* Overlay Menu */}
      {menuOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-95 z-40 flex flex-col items-center justify-center transition-all">
          <button
            className="absolute top-5 right-5 text-white hover:text-purple-400"
            onClick={() => setMenuOpen(false)}
            aria-label="Close Menu"
          >
            {CLOSE_ICON}
          </button>
          <div className="flex flex-col items-center gap-8 mt-12">
            {NAV_LINKS.map((link) => (
              <Link
                key={link.label}
                href={link.href}
                className="text-2xl font-semibold text-white hover:text-purple-400 transition-colors"
                onClick={() => setMenuOpen(false)}
              >
                {link.label}
              </Link>
            ))}
          </div>
        </div>
      )}
    </>
  );
};

export default MobileNavbar;
